import Finisher from "../../components/finisher"
import Topbar from "../../components/topbar"
import './style.css';
import articles from "../../db/articles";
import ListItem from "../../components/article/list";
import { setDescription, setTitle , setKeywords } from "../../lib/seo";
import { useState } from "react";
import MobileNav from "../../components/mobile-nav";
import Footer from "../../components/footer";

export default (props) => {
    setTitle('Articles');
    setDescription('View a list of articles');
    setKeywords('Ninjas in Pyjamas');
    const [navOpen , setNavOpen]  = useState(false);
    return (
        <div className="article-root">
            <Finisher skew={0} style={{ height: '250px' }}>
                <h1 className="page-title">Articles</h1>
            </Finisher> 
            <Topbar onNavigation={(ev) => { setNavOpen(!navOpen); }} />
            <MobileNav onNavigationClose={(ev) => { setNavOpen(!navOpen); }} open={navOpen} />
            <div className="content">
                <div className="centre">
                    <div style={{ height: '100px' }}></div>
                    {articles.map((article , idx) => {
                        return (
                            <ListItem article={article} articleId={idx}/>
                        );
                    })}
                </div>
            </div>
            <br /><br />
            <br /><br />
            <br /><br />
            <br /><br />
            <Footer />
        </div>
    )
}