import Topbar from "../../components/topbar"
import Finisher from "../../components/finisher"
import videos from "../../db/videos";
import './style.css';

import { useState } from "react";
import MobileNav from "../../components/mobile-nav";

function extractIdFromUrl() {
    const parts = window.location.pathname.split('/');
    const id = parts[3];
  
    return id;
}
  
export default (props) => {
    let { videoId } = props;
    
    if ( !videoId ) {
        videoId = extractIdFromUrl();
    }

    const video = videos[videoId];

    const [navOpen , setNavOpen]  = useState(false);

    return (
        <div className="theatre">
            <Finisher skew={0} style={{ height: '70px' }}></Finisher>
            <Topbar onNavigation={(ev) => { setNavOpen(!navOpen); }} />
            <MobileNav onNavigationClose={(ev) => { setNavOpen(!navOpen); }} open={navOpen} />
            <div className="video-player">
                <iframe autoplay={true} src={"https://www.youtube.com/embed/" + video.config.vendorId + '?autoplay=1'} allowFullScreen={true} frameBorder={0} style={{
                    width: '100%' , 
                    height: window.innerWidth < 640 ? '100%' : 'calc(100% + 120px)' , 
                    top: window.innerWidth < 640 ? '0px' : '-80px' ,
                    position: 'relative' }}></iframe>
            </div>
            <div className="video-desc">
                <h2>{video.meta.title}</h2>
                <p>{video.meta.description}</p>
            </div>
        </div>
    )
}