import { Link } from "react-router-dom";
import './style.css';
import { ROOT } from "../../../env";

export default (props) => {
    const { article } = props;
    const { meta , sections } = article;
    return (
        <div className="article">
            <div className="article-img">
                <img alt={meta.title} src={meta.image}/>
            </div>
            <div className="article-content">
                <Link to={ROOT + "article/" + props.articleId}><h2>{meta.title}</h2></Link>
                <label>{meta.author}</label>
                <p>{meta.description}</p>
                <div className="tags">
                    {meta.keywords.map((tag) => {
                        return <span className='article-tag'>{tag}</span>
                    })}
                </div>
            </div>
        </div>
    )
}