import Finisher from "../../components/finisher";
import Topbar from "../../components/topbar";
import { setDescription, setKeywords, setTitle } from "../../lib/seo";
import banner from './../../media/offline.png';
import './style.css';

import { useState } from "react";
import MobileNav from "../../components/mobile-nav";

export default (props) => {
    setTitle("Live Stream");
    setDescription("View a list of donors and sponsors that support Ninja's in Pyjamas");
    setKeywords('Ninjas In Pyjamas , Sponsors, Donors, Sponsorship');

    const [navOpen , setNavOpen]  = useState(false);

    return (
        <div className="live">
            <Finisher skew={0} style={{ height: '70px' }}></Finisher>
            <Topbar onNavigation={(ev) => { setNavOpen(!navOpen); }} />
            <MobileNav onNavigationClose={(ev) => { setNavOpen(!navOpen); }} open={navOpen} />
            <div className="banner">
                <img src={banner}/>
                <div className="cover">
                    
                </div>
            </div>
        </div>
    )
}