        setHms([
            '01:', 
            formatted.getMinutes() < 10 ? '0' + formatted.getMinutes() : formatted.getMinutes() , 
            ':' , 
            formatted.getSeconds() < 10 ? '0' + formatted.getSeconds() : formatted.getSeconds()
        ].join(''));
