import { Component, createRef } from "react";
import { init , start } from './script';

export default class Finisher extends Component {
    constructor(props){
        super(props);
        this.state = {

        }
        this.ref = createRef();
        init();
    }
    componentDidMount(){
        start({
          "count": 100,
          "size": {
            "min": 2,
            "max": 8,
            "pulse": 0
          },
          "speed": {
            "x": {
              "min": 0,
              "max": 0.4
            },
            "y": {
              "min": 0,
              "max": 0.6
            }
          },
          "colors": {
            "background": "#0d0f01",
            "particles": [
              "#8cff68",
              "#e3ff2d",
              "#e0ff67"
            ]
          },
          "blending": "overlay",
          "opacity": {
            "center": 1,
            "edge": 0
          },
          "skew": this.props.skew ?? -0.7,
          "shapes": [
            "c",
            "s",
            "t"
          ]
        });
    }
    componentWillUnmount(){
        if ( this.ref.current ) {
            this.ref.current.innerHTML = '';
        }
    }
    render(){
        return (
            <div className={"finisher-hero " + this.props.className} style={this.props.style}>
              <div ref={this.ref} className="finisher-header" style={this.props.style}>

              </div>
              <div style={{ position: 'absolute' , top: '0px' , width: '100vw' }} class="overlay">
                <div className="centre">
                    {this.props.children}
                </div>
              </div>
            </div>
        )
    }
}