import { Link } from 'react-router-dom';
import './style.css';
import { ROOT } from '../../env';
import { useState } from 'react';

const listeners = {
    'resize': []
};
window.addEventListener('resize' , () => {
    listeners.resize.forEach((subscriber) => {
        subscriber();
    });
});
export default (props) => {
    
    const [ w , setWidth ] = useState(window.innerWidth);
    
    if ( listeners.resize.length == 0 ) {
        listeners.resize.push(() => {
            setWidth(window.innerWidth);
        });
    }
    if ( w > 640 ) {
        return <></>;
    }
    return (
        <div className={"navigation " + (props.open ? 'open' : '')}>
            <button onClick={props.onNavigationClose}><i className="fas fa-xmark"></i></button>
            <div className='nav-header'>
                Navigation
            </div>
            <div className='nav-links'>
                <Link to={ROOT + ""}>Home</Link>
                <Link to={ROOT + "blog"}>Blog</Link>
                <Link to={ROOT + "videos"}>Videos</Link>
                <Link to={ROOT + "live"}>Live</Link>
                <Link to={ROOT + "sponsors"}>Sponsors</Link>
            </div>
        </div>
    )
}