import { Link } from "react-router-dom";
import './style.css';
import { ROOT } from "../../../env";

export default (props) => {
    const { video } = props;
    const { meta , config } = video;
    return (
        <div className="video-item">
            <div className="thumbnail-img">
                <img alt={meta.title} src={meta.image}/>
            </div>
            <div className="video-content">
                <Link to={ROOT + "video/" + props.videoId}><h2>{meta.title}</h2></Link>
                <label>{meta.author}</label>
                <p>{meta.description}</p>
                <div className="tags">
                    {meta.keywords.length > 0 && meta.keywords[0] !== '' ? meta.keywords.map((tag) => {
                        return <span className='article-tag'>{tag}</span>
                    }) : null}
                </div>
            </div>
        </div>
    )
}