

export const setTitle = (title) => {
    document.title = title;
    window.title = title;
    getMeta('og:title').setAttribute('content' , title);
    getMeta('twitter:title').setAttribute('content' , title);
}
export const setDescription = (description) => {
    getMeta('description').setAttribute('content' , description);
    getMeta('og:description').setAttribute('content' , description);
    getMeta('twitter:title').setAttribute('content' , description);
}
export const setKeywords = (keywords) => {
    getMeta('keywords').setAttribute('content' , keywords);
}


const getMeta = (name) => {
    let target = document.querySelector('meta[name="' + name + '"]');

    if ( !target ) {
        target = document.createElement('meta');
        target.setAttribute('name' , name);
        document.head.appendChild(target);
    }
    return target;
}