import { useState } from "react"
import './style.css';
import logo from './../../media/logo.png';
import { Link } from "react-router-dom";
import { ROOT } from "../../env";

export default (props) => {

    const [yPos , setYPos] = useState(0);
    const [w , setW] = useState(window.innerWidth);

    window.onscroll = (ev) => {
        setYPos(window.scrollY);
    }

    window.onresize = (ev) => {
        setW(window.innerWidth);
    }
    
    if ( w < 641 ) {
        return (
            <div className={"topbar mobile " + (yPos > 50 ? "scrolled" : "")}>
                <button onClick={props.onNavigation}><i className="fas fa-bars"></i></button>
                <div className="logo">
                    <Link to={ROOT}><img alt="Ninjas in Pyjamas Logo" src={logo}/></Link>
                </div>
            </div>
        )
    } else {
        return (
            <div className={"topbar " + (yPos > 100 ? "scrolled" : "")}>
                <div className="centre">
                    <div className="links-left">
                        <Link to={ROOT + ""}>Home</Link>
                        <Link to={ROOT + "blog"}>Blog</Link>
                        <Link to={ROOT + "videos"}>Videos</Link>
                        <Link to={ROOT + "live"}>Live</Link>
                        <Link to={ROOT + "sponsors"}>Sponsors</Link>
                    </div>
                    <div className="logo">
                        <Link to={ROOT}><img alt="Ninjas in Pyjamas Logo" src={logo}/></Link>
                    </div>
                    <div className="links-right">
                        <Link target="_blank" to="https://www.facebook.com/#ninjasinpyjamas"><i className="fab fa-facebook"></i></Link>
                        <Link target="_blank" to="https://instagram.com/#ninjasinpyjamas"><i className="fab fa-instagram"></i></Link>
                        <Link target="_blank" to="https://www.youtube.com/#ninjasinpyjamas"><i className="fab fa-youtube"></i></Link>
                        <Link target="_blank" to="https://beta.snapchat.com/#ninjasinpyjamas"><i className="fab fa-snapchat"></i></Link>
                        <Link target="_blank" to="https://twitch.tv/#ninjasinpyjamas"><i className="fab fa-twitch"></i></Link>
                        <Link target="_blank" to="https://pinterest.com/#ninjasinpyjamas"><i className="fab fa-pinterest"></i></Link>
                    </div>
                </div>
            </div>
        );
    }
}