import { createRef, useState } from 'react';
import './style.css';

const tick = {
    handle: null
}
setInterval(() => {
    if ( tick.handle ) {
        tick.handle();
    } else {
        console.log('No handle');
    }
} , 1000 / 60);
const container = createRef();
export default (props) => {

    const [ left , setLeft ] = useState(0);
    tick.handle = () => {
        
        if (container.current && left > container.current.getBoundingClientRect().width ) {
            setLeft(0);
        } else {
            setLeft(left + 3);
        }
        
    };
    return (
        <div ref={container} className={"radar " + (props.className ?? '')} style={props.style}>
            <img alt={props.alt} src={props.map}/>
            <div className='scanner-overlay'>
                {props.playerLocations.map((location) => {
                    return (
                        <div style={{ left: location.x , top: location.y , opacity: (location.x <= left ? 1 : 0) }} className="blip">

                        </div>
                    )
                })}
                <div className="scanner-beam" style={{ left: left + 'px' }}></div>
            </div>
        </div>
    )
}